import React, { useContext, useState, ChangeEvent } from 'react';
import Multiselect from 'multiselect-react-dropdown';
import ReactMde from 'react-mde';
import Showdown from 'showdown';
import 'react-mde/lib/styles/css/react-mde-all.css';
import { SearchContext } from '../../context/SearchContext';
import SocialInput from './SocialInput';
import { ProjectContext } from '../../context/ProjectContext';
import { uploadBytesResumable, ref } from 'firebase/storage';
import { storage } from '../../hooks/useFirebase';
import { getDownloadURL } from 'firebase/storage';
import { navigate } from 'gatsby';
import Layout from '../../components/layout';

interface LinkState {
  website: string;
  github: string;
  facebook: string;
  instagram: string;
  twitter: string;
  tiktok: string;
  telegram: string;
  youtube: string;
  reddit: string;
  discord: string;
  linkedin: string;
  appstore: string;
  playstore: string;
}

interface Option {
  name: string;
  id: string;
}

const NewProjectForm: React.FC = () => {
  const [selectedCategoryOptions, setSelectedCategoryOptions] = useState<string[]>([]);
  const [selectedTagOptions, setSelectedTagOptions] = useState<string[]>([]);
  const [logoImage, setLogoImage] = useState<File | null>(null);
  const [coverImage, setCoverImage] = useState<File | null>(null);
  const [additionalImages, setAdditionalImages] = useState<File[]>([]);
  const [projectName, setProjectName] = useState<string>('');
  const [overview, setOverview] = useState<string>('');
  const [tldr, setTldr] = useState<string>('');
  const [logoUrl, setLogoUrl] = useState<string>('');
  const [coverUrl, setCoverUrl] = useState<string>('');
  const [additionalUrls, setAdditionalUrls] = useState<string[]>([]);
  const [links, setLinks] = useState<LinkState>({
    website: '',
    github: '',
    facebook: '',
    instagram: '',
    twitter: '',
    tiktok: '',
    telegram: '',
    youtube: '',
    reddit: '',
    discord: '',
    linkedin: '',
    appstore: '',
    playstore: ''
  });
  const [uploadProgress, setUploadProgress] = useState<number>(0);
  const [loading, setLoading] = useState<boolean>(false); // State to control the loading overlay
  const [selectedTabOverview, setSelectedTabOverview] = useState<'write' | 'preview'>('write');
  const [selectedTabTldr, setSelectedTabTldr] = useState<'write' | 'preview'>('write');

  const { setListModal, tags, coreCategory } = useContext(SearchContext);
  const { createNewProject } = useContext(ProjectContext);

  function closeModal() {
    setListModal(false);
  }

  const handleAddCategoryOption = (selectedList: Option[]) => {
    setSelectedCategoryOptions(selectedList.map(option => option.id));
  };

  const handleRemoveCategoryOption = (selectedList: Option[]) => {
    setSelectedCategoryOptions(selectedList.map(option => option.id));
  };

  const handleAddTagOption = (selectedList: Option[]) => {
    setSelectedTagOptions(selectedList.map(option => option.id));
  };

  const handleRemoveTagOption = (selectedList: Option[]) => {
    setSelectedTagOptions(selectedList.map(option => option.id));
  };

  const handleLogoChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setLogoImage(e.target.files[0]);
    }
  };

  const handleCoverChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setCoverImage(e.target.files[0]);
    }
  };

  const handleAdditionalImagesChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const files = Array.from(e.target.files).slice(0, 3);
      setAdditionalImages(files);
    }
  };

  const Loader = ({ progress }: { progress: number }) => {
    return (
      <div className="loader-overlay">
        <div className="loader">
          <p>Uploading {progress}% done</p>
        </div>
      </div>
    );
  };

  const handleUpload = async (imageToUpload: File | null, type: number, index?: number) => {
    console.log("here - ", imageToUpload)
    setLoading(true);
    if (imageToUpload) {
      const storageRef = ref(storage, `images/${Date.now()}`);
      const uploadTask = uploadBytesResumable(storageRef, imageToUpload);
  
      uploadTask.on(
        'state_changed',
        (snapshot) => {
          const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log('Upload is ' + progress + '% done');
          setUploadProgress(progress);
        },
        (error) => {
          console.error('Upload failed', error);
        },
        () => {
          getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
            if (type === 1) {
              setLogoUrl(downloadURL);
              setLogoImage(null);
            } else if (type === 2) {
              setCoverUrl(downloadURL);
              setCoverImage(null)
            } else if (type === 3 && index !== undefined) {
              setAdditionalUrls((prevUrls) => {
                const newUrls = [...prevUrls];
                newUrls[index] = downloadURL;
                return newUrls;
              });
            }
            console.log('File available at', downloadURL);
            setLoading(false);
          });
        }
      );
    }
  };
  
  const handleCreateProject = async () => {
    if (projectName && overview && tldr && logoUrl && coverUrl && selectedCategoryOptions.length && selectedTagOptions.length) {
      const obj = {
        name: projectName,
        coreCategories: selectedCategoryOptions,
        tags: selectedTagOptions,
        tldr,
        overview,
        logo: logoUrl,
        coverImg: coverUrl,
        screenshots: additionalUrls,
        links,
      };
      createNewProject(obj);
      alert('Project Added!');
      navigate('/');
      return;
    }
    alert('Please fill out the required fields');
  };

  const inputTags = tags?.map(item => ({ name: item.name, id: item._id }));
  const inputCategories = coreCategory?.map(item => ({ name: item.name, id: item._id }));
  
  const converter = new Showdown.Converter();

  return (
    <Layout>
    <div className='md:mx-[20%] mt-12 mb-12'>
      {loading && <div className="overlay"></div>}
      {loading && <Loader progress={uploadProgress} />}
      <div className='mx-4'>
        <h2 className='font-bold text-primarydark text-center md:text-left'>Add a Project</h2>
        <div className='mt-4 grid grid-cols-1 md:grid-cols-2 gap-4'>
          <div className='col-span-1 md:col-span-2'>
            <label className='text-xs'>Project name</label>
            <input
              placeholder='Project name'
              value={projectName}
              onChange={(e) => setProjectName(e.target.value)}
              className='border-[1px] w-full text-sm p-2 rounded-sm outline-none'
            />
          </div>
          <div className='col-span-1 md:col-span-1'>
            <label className='text-xs'>Categories</label>
            <Multiselect
              options={inputCategories}
              selectedValues={selectedCategoryOptions?.map(id => inputCategories?.find(cat => cat.id === id))}
              onSelect={handleAddCategoryOption}
              onRemove={handleRemoveCategoryOption}
              displayValue="name"
              className="border-[1px] w-full text-sm p-2 rounded-sm outline-none"
            />
          </div>
          <div className='col-span-1 md:col-span-1'>
            <label className='text-xs'>Tags</label>
            <Multiselect
              options={inputTags}
              selectedValues={selectedTagOptions.map(id => inputTags?.find(tag => tag.id === id))}
              onSelect={handleAddTagOption}
              onRemove={handleRemoveTagOption}
              displayValue="name"
              className="border-[1px] w-full text-sm p-2 rounded-sm outline-none"
            />
          </div>
          <div className='col-span-1 md:col-span-2'>
            <label className='text-xs'>Project TL;DR</label>
            <ReactMde
              value={tldr}
              onChange={setTldr}
              selectedTab={selectedTabTldr}
              onTabChange={setSelectedTabTldr}
              generateMarkdownPreview={(markdown) => Promise.resolve(converter.makeHtml(markdown))}
              childProps={{
                writeButton: {
                  tabIndex: -1
                }
              }}
            />
          </div>
          <div className='col-span-1 md:col-span-2'>
            <label className='text-xs'>Project Overview</label>
            <ReactMde
              value={overview}
              onChange={setOverview}
              selectedTab={selectedTabOverview}
              onTabChange={setSelectedTabOverview}
              generateMarkdownPreview={(markdown) => Promise.resolve(converter.makeHtml(markdown))}
              childProps={{
                writeButton: {
                  tabIndex: -1
                }
              }}
            />
          </div>
          <div className='col-span-1 md:col-span-1'>
            <label className='text-xs'>Logo Image</label>
            <div className="flex items-center">
              <input
                type='file'
                accept=".jpg,.jpeg,.png"
                onChange={handleLogoChange}
                className='border-[1px] w-full text-sm p-2 rounded-sm outline-none'
              />
              <button 
                onClick={() => handleUpload(logoImage, 1)} 
                className="ml-2 px-4 py-2 bg-primarylight text-white rounded-md"
                disabled={!logoImage}
              >
                Upload
              </button>
            </div>
            {logoUrl && <img src={logoUrl} alt="Logo Preview" className="mt-2 w-full" />}
          </div>
          <div className='col-span-1 md:col-span-1'>
            <label className='text-xs'>Cover Image</label>
            <div className="flex items-center">
              <input
                type='file'
                accept=".jpg,.jpeg,.png"
                onChange={handleCoverChange}
                className='border-[1px] w-full text-sm p-2 rounded-sm outline-none'
              />
              <button 
                onClick={() => handleUpload(coverImage, 2)} 
                className="ml-2 px-4 py-2 bg-primarylight text-white rounded-md"
                disabled={!coverImage}
              >
                Upload
              </button>
            </div>
            {coverUrl && <img src={coverUrl} alt="Cover Preview" className="mt-2 w-full" />}
          </div>
          <div className='col-span-1 md:col-span-2'>
            <label className='text-xs'>Additional Images (up to 3)</label>
            <div className="flex items-center">
              <input
                type='file'
                accept=".jpg,.jpeg,.png"
                multiple
                onChange={handleAdditionalImagesChange}
                className='border-[1px] w-full text-sm p-2 rounded-sm outline-none'
              />
              <button 
                onClick={() => additionalImages.forEach((image, index) => handleUpload(image, 3, index))} 
                className="ml-2 px-4 py-2 bg-primarylight text-white rounded-md"
                disabled={additionalImages.length === 0}
              >
                Upload
              </button>
            </div>
            {additionalUrls.map((url, index) => (
              <img key={index} src={url} alt={`Additional Image ${index + 1}`} className="mt-2 w-full" />
            ))}
          </div>
        </div>
        <SocialInput links={links} setLinks={setLinks} />
        <div className='flex flex-col md:flex-row mt-4 space-y-2 md:space-y-0 md:space-x-2'>
          <button onClick={closeModal} className='bg-white w-full p-2 rounded-sm border-[1px]'>Close</button>
          <button onClick={handleCreateProject} className='bg-primarylight text-white w-full p-2 rounded-sm'>Create</button>
        </div>
      </div>
    </div>
    </Layout>
  );
}

export default NewProjectForm;
