import React, { useState } from 'react';
import { TbWorldWww } from 'react-icons/tb';
import { FaGithub, FaFacebookF, FaInstagram, FaReddit } from 'react-icons/fa';
import { FaXTwitter, FaDiscord } from 'react-icons/fa6';
import { IoLogoTiktok, IoLogoYoutube } from 'react-icons/io5';
import { BiLogoTelegram } from 'react-icons/bi';
import { FaLinkedin } from "react-icons/fa";
import { FaAppStoreIos } from "react-icons/fa";
import { IoLogoGooglePlaystore } from "react-icons/io5";

const iconMap = {
  website: { component: <TbWorldWww className='text-gray-600 rounded-md' />, placeholder: 'www.yoursite.com', label: 'Website' },
  github: { component: <FaGithub className='text-gray-600 rounded-md' />, placeholder: 'username', label: 'GitHub' },
  facebook: { component: <FaFacebookF className='text-gray-600 rounded-md' />, placeholder: 'username', label: 'Facebook' },
  instagram: { component: <FaInstagram className='text-gray-600 rounded-md' />, placeholder: 'username', label: 'Instagram' },
  twitter: { component: <FaXTwitter className='text-gray-600 rounded-md' />, placeholder: 'username', label: 'Twitter' },
  tiktok: { component: <IoLogoTiktok className='text-gray-600 rounded-md' />, placeholder: 'username', label: 'TikTok' },
  telegram: { component: <BiLogoTelegram className='text-gray-600 rounded-md' />, placeholder: 'username', label: 'Telegram' },
  youtube: { component: <IoLogoYoutube className='text-gray-600 rounded-md' />, placeholder: 'Channel Id', label: 'YouTube' },
  reddit: { component: <FaReddit className='text-gray-600 rounded-md' />, placeholder: 'username', label: 'Reddit' },
  discord: { component: <FaDiscord className='text-gray-600 rounded-md' />, placeholder: 'username', label: 'Discord' },
  linkedin: { component: <FaLinkedin className='text-gray-600 rounded-md' />, placeholder: 'username', label: 'Linkedin'},
  appstore: { component: <FaAppStoreIos className='text-gray-600 rounded-md' />, placeholder: 'app store link', label: 'App Store'},
  playstore: { component: <IoLogoGooglePlaystore className='text-gray-600 rounded-md' />, placeholder: 'play store link', label: 'Play Store'},
};

const SocialInput = ({ links = {}, setLinks }) => {
  const [activeInputs, setActiveInputs] = useState(['website', 'twitter', 'youtube', 'linkedin']);

  const handleAddInput = (type) => {
    if (!activeInputs.includes(type)) {
      setActiveInputs([...activeInputs, type]);
    }
  };

  const handleRemoveInput = (type) => {
    setActiveInputs(activeInputs.filter(input => input !== type));
    setLinks({ ...links, [type]: '' });
  };

  const handleInputChange = (type, value) => {
    setLinks({ ...links, [type]: value });
  };

  // Initialize links with default values for each type if not already defined
  const initializedLinks = {};
  Object.keys(iconMap).forEach(type => {
    initializedLinks[type] = links[type] || '';
  });

  return (
    <>
      <h1 className='text-primarydark font-semibold mt-6'>Social</h1>
      <div className='grid grid-cols-1 md:grid-cols-2 gap-4 w-full'>
        {activeInputs.map((type) => (
          <div key={type} className='flex flex-col'>
            <label className='text-xs'>{iconMap[type].label}</label>
            <div className='flex items-center'>
              <div className='p-2'>{iconMap[type].component}</div>
              <input
                placeholder={iconMap[type].placeholder}
                value={initializedLinks[type]}
                onChange={(e) => handleInputChange(type, e.target.value)}
                className='border-[1px] text-sm p-2 rounded-md outline-none flex-grow'
              />
              <button onClick={() => handleRemoveInput(type)} className='text-red-500 p-2'>✕</button>
            </div>
          </div>
        ))}
      </div>
      <div>
        <h2 className='text-xs mt-4'>Add more</h2>
        <div className='flex flex-wrap justify-between'>
          {Object.keys(iconMap).map((type) => (
            <button
              key={type}
              onClick={() => handleAddInput(type)}
              className={`bg-gray-200 rounded-md w-fit p-1 ${activeInputs.includes(type) ? 'opacity-50 cursor-not-allowed' : ''}`}
              disabled={activeInputs.includes(type)}
            >
              {iconMap[type].component}
            </button>
          ))}
        </div>
      </div>
    </>
  );
}

export default SocialInput;
